<template>
  <div class="dishes">
    <h3 class="d-flex">
      За все продукты
      <strong class="ml-auto text-success">{{ dishes.dishes_portion }} г.</strong>
    </h3>
    <div class="d-flex">
      <div class="dishes-photo mr-2">
        <img class="img-fluid d-block" v-if="dishes.image" :src="dishes.image"/>
      </div>
      <div class="product-information w-100 mb-3 d-flex text-center">
        <div class="information flex-fill d-flex flex-column">
          <span class="information__header">БЖ</span>
          <span class="information__footer">{{ calcFixed(bj_var) }}</span>
        </div>
        <div class="information flex-fill d-flex flex-column">
          <span class="information__header">У</span>
          <span class="information__footer">{{ calcFixed(y_var) }}</span>
        </div>
        <div class="information flex-fill d-flex flex-column">
          <span class="information__header">ХЕ</span>
          <span class="information__footer">{{ calcFixed(xe_var) }}</span>
        </div>
      </div>
    </div>

    <form @submit="setDishesPouchDB">
      <div class="mb-3">
        <label for="dishesName" class="form-label">Название блюда</label>
        <input required v-model="dishes.title" type="text" class="form-control" id="dishesName" placeholder="">
      </div>
      <div class="mb-3">
        <label for="dishesAvatar" class="form-label">Фото</label>
        <b-form-file required browse-text="Выбрать" placeholder="Выбрать картинку" @change="onChange" ref="file-input" id="dishesAvatar" class="mb-2"></b-form-file>
      </div>
      <div class="mb-3">
        <b-button type="submit" class="w-100" variant="primary" :disabled="isCreated">
          <b-spinner style="width: 25px; height: 25px;" type="grow" v-show="isCreated"></b-spinner>
          Добавить
        </b-button>
      </div>
    </form>
    <h3 class="d-flex align-items-center">
       Входит в блюдо
    </h3>
    <div class="dishes-items" v-if="products.length">
      <div v-for="(item, index) in products" :key="index">
        <div class="d-block dishes-item card text-decoration-none mb-2">
          <div class="card-body d-flex">
            <div class="item-avatar mr-2">
              <img class="d-block img-fluid mb-1" v-if="item.image" :src="item.image"/>
              <b-button @click="removeItemsDishes(index)" size="sm" variant="danger" class="w-100 text-center">
                <b-icon icon="x-circle" aria-hidden="true"></b-icon>
              </b-button>
            </div>
            <div class="item-content w-100 d-flex flex-column">
              <h6>{{item.title}}</h6>
              <div class="mt-auto">
                <p class="d-flex align-items-center justify-content-end mb-2">
                  <span class="mr-1">на</span>
                  <input @change="onChangeCalculate" v-model="item.dishes_gram" type="number" class="form-control dishes-gram">
                  <span class="ml-1">г.</span>
                </p>
                <div class="item-information d-flex text-center">
                  <div class="item-information__item flex-fill d-flex flex-column">
                    <span class="item-information__header">БЖ</span>
                    <span class="item-information__footer">{{ bxbjCalc(bxCalc(item.product_b, item.dishes_gram), bjCalc(item.product_j, item.dishes_gram))}}</span>
                  </div>
                  <div class="item-information__item flex-fill d-flex flex-column">
                    <span class="item-information__header">У</span>
                    <span class="item-information__footer">{{ byCalc(item.product_y, item.dishes_gram)}}</span>
                  </div>
                  <div class="item-information__item flex-fill d-flex flex-column">
                    <span class="item-information__header">ХЕ</span>
                    <span class="item-information__footer">{{ allCalc(bxCalc(item.product_b, item.dishes_gram),bjCalc(item.product_j, item.dishes_gram),byCalc(item.product_y, item.dishes_gram))}}</span>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'DishesNew',
  data() {
    return {
      isCreated: false,
      bj_var: 0,
      y_var: 0,
      xe_var: 0,
      dishes: {
        title: '',
        image: null,
        itemIds: [],
        dishes_portion: 0,
        b_var: 0,
        j_var: 0,
        y_var: 0,
      },
    }
  },
  computed: {
    products() {
      return this.$store.state.productInfo;
    },
    isOnline() {
      return window.navigator.onLine;
    },
    bx_param: {
      get() {
        return this.$store.state.option.bx_param
      }
    },
    bj_param: {
      get() {
        return this.$store.state.option.bj_param
      }
    }
  },
  methods: {
    resizeImage(base64Str, maxWidth = 100, maxHeight = 100) {
      return new Promise((resolve) => {
        let img = new Image()
        img.src = base64Str
        img.onload = () => {
          let canvas = document.createElement('canvas')
          const MAX_WIDTH = maxWidth
          const MAX_HEIGHT = maxHeight
          let width = img.width
          let height = img.height
          //
          if (width > height) {
            if (width > MAX_WIDTH) {
              height *= MAX_WIDTH / width
              width = MAX_WIDTH
            }
          } else {
            if (height > MAX_HEIGHT) {
              width *= MAX_HEIGHT / height
              height = MAX_HEIGHT
            }
          }
          canvas.width = width
          canvas.height = height
          let ctx = canvas.getContext('2d')
          ctx.drawImage(img, 0, 0, width, height)
          resolve(canvas.toDataURL())
        }
      })
    },
    getBase64(file) {
      return new Promise(function (resolve, reject) {
        let reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = () => resolve(reader.result)
        reader.onerror = (error) => reject('Error: ', error);
      })
    },

    calculate(value, gram) {
      return (parseFloat(value) * gram) / 100
    },
    bxCalc(value, gram) {
      return ((this.calculate(value, gram) * parseFloat(this.bx_param)) / 12).toFixed(1);
    },
    bjCalc(value, gram) {
      return ((this.calculate(value, gram) * parseFloat(this.bj_param)) / 12).toFixed(1);
    },
    byCalc(value, gram) {
      return (this.calculate(value, gram) / 12).toFixed(1);
    },
    bxbjCalc(p1, p2) {
      return (parseFloat(p1) + parseFloat(p2)).toFixed(1);
    },
    allCalc(p1, p2, p3) {
      return (parseFloat(p1) + parseFloat(p2) + parseFloat(p3)).toFixed(1);
    },
    calcFixed(v) {
      return v.toFixed(1);
    },
    plusAllCalculate(items) {
      if(items.length) {
        this.bj_var = 0
        this.y_var = 0
        this.xe_var = 0
        this.dishes.b_var = 0;
        this.dishes.j_var = 0;
        this.dishes.y_var = 0;

        this.dishes.dishes_portion = 0
        items.forEach((item)=> {
          this.bj_var += (parseFloat(this.bxCalc(item.product_b, item.dishes_gram)) + parseFloat(this.bjCalc(item.product_j, item.dishes_gram)));
          this.y_var += (parseFloat(this.byCalc(item.product_y, item.dishes_gram)));
          this.xe_var += (parseFloat(this.bxCalc(item.product_b, item.dishes_gram)) + parseFloat(this.bjCalc(item.product_j, item.dishes_gram)) + parseFloat(this.byCalc(item.product_y, item.dishes_gram)))

          this.dishes.b_var += parseFloat(item.product_b);
          this.dishes.j_var += parseFloat(item.product_j);
          this.dishes.y_var += parseFloat(item.product_y);

          this.dishes.dishes_portion += parseInt(item.dishes_gram);
        })
      }
    },
    //add
    removeItemsDishes(index) {
      this.$store.dispatch('setProductRemove', index);
    },
    reset() {
      this.dishes.title = '';
      this.dishes.image = null;
      this.$refs['file-input'].reset();
    },
    onChange(event) {
      const self = this;
      this.getBase64(event.target.files[0]).then(images => {
        self.resizeImage(images).then(img => {
          self.dishes.image = img;
        })
      })
    },
    onChangeCalculate(){
      this.plusAllCalculate(this.products);
    },

    async setDishesPouchFB(id) {
      try {
        await this.$firebase.database().ref().child(`dishes/${id}`).update({
          _id: id,
          ...this.dishes
        })
      } catch (error) {
        console.error(error)
      }
    },
    async setDishesPouchDB(evt) {
      evt.preventDefault();
      this.isCreated = true;
      try {
        this.products.forEach((value) => {
          this.dishes.itemIds.push({id: value._id, dishes_gram: value.dishes_gram});
        })
        await this.$pouch.post({
          ...this.dishes
        }, {}, 'dishes').then((res) => {
          if (this.isOnline) {
            this.setDishesPouchFB(res.id).then(() => {
              this.reset();
              this.isCreated = false;
              this.$store.dispatch('setProductRemoveAll');
              this.$router.push({name: 'Dishes', params: {id: res.id}})
            })
          } else {
            this.reset();
            this.isCreated = false;
            this.$store.dispatch('setProductRemoveAll');
            this.$router.push({name: 'Dishes', params: {id: res.id}})
          }
        });
      } catch (error) {
        console.error(error)
      }
    }
  },
  mounted() {
    this.plusAllCalculate(this.products);
  }
}
</script>

<style scoped lang="scss">
.dishes {
  .dishes-photo {
    min-width: 100px;
    width: 70px;
  }
  .card-body {
    padding: 5px;
  }
  .calculate-hr {
    background-color: #d1e7dd;
    height: 5px;
  }
  .product-information {
    .information {
      margin: 0 2px;
      .information__header {
        border-bottom: 1px solid green;
      }
      .information__footer {
        font-weight: 500;
        font-size: 18px;
      }
    }
  }
  .dishes-items {
    .dishes-item {
      color: #000000;

      .item-avatar {
        width: 70px;
        min-width: 70px;
      }
      .item-information {
        &__item {
          margin: 0 2px;
          .item-information__header {
            border-bottom: 1px solid green;
          }
          .item-information__footer {
            font-weight: 500;
            font-size: 16px;
          }
        }
      }
      .dishes-gram {
        max-width: 100px;
      }
    }
  }
}
</style>
